.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

body,
html,
#root {
	height: 100%;
}

html {
	font-size: 18px;
}

h1 {
	padding: 0.77rem 0;
	background-color: #010c3f;
	text-align: center;
	font-family: 'Poppins', sans-serif;
	font-size: 1.88rem;
	color: #fff;
}

h1 .highlight {
	color: #6c41ec;
}

h2 {
	font-family: 'Poppins', sans-serif;
	font-size: 1.55rem;
}

.App {
	height: 100%;
	padding: 0 17% 10% 17%;
	background-image: url('./music-background-desktop.jpeg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	font-family: 'Work Sans', sans-serif;
	font-weight: 500;
	color: #fff;
}

.App-playlist {
	display: flex;
	justify-content: space-between;
	width: 100%;
}

@media only screen and (max-width: 1020px) {
	.App-playlist {
		align-items: center;
		flex-direction: column;
	}
}